import React,{useState} from 'react';
import {Form} from "./timer";
import styles from "./FormList.module.css";

function TimerStart(props) {
    const [inp, setInp] = useState(true);
    const sub = (data) => {
        console.log(data);
        let sec = data.hor*60*60+data.min*60+(+data.sec);
        props.newTimersThink(sec,props.id);
        setInp((r=>!r));
    };
    return (
        <div>
            {
                (inp) ? (
                    <img className={styles.hov} onClick={()=>setInp((r)=>!r)} src="data:image/svg+xml;utf8;base64,"  alt=''/>
                ) : (
                    <Form onSubmit={sub} className={styles.timerForm} initialValues={{hor:'00',min:'00',sec:'00'}}/>
                )
            }
        </div>
    );
}

export default TimerStart;